@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;padding:0;box-sizing:border-box;background-color:#000;color:#fff}.App{font-family:Montserrat,Droid Sans,sans-serif;height:90vh;margin:.5rem;display:flex;flex-direction:column;grid-gap:.5rem;gap:.5rem}h2,button{font-weight:normal;font-family:Audiowide,Arial,Helvetica,sans-serif;text-align:center;margin:0;padding:1rem;border:none;background-color:#ff0;color:#000}h3{margin:.5rem auto}li{list-style:square;text-align:left}li:empty{list-style:none;height:1rem}.home-button{font-size:2rem;border-radius:3rem 0 0 0}nav button{font-size:1.5rem;width:100%}nav button:first-child{border-radius:0 0 0 2rem}.button-effect{transition:300ms ease-in-out}.button-effect:hover,.button-effect:focus{background-color:aqua}.selected{background-color:aqua}p{margin:0}main{flex:100 1 auto;overflow:hidden;display:flex;justify-content:stretch}.decor-box{flex:1 0;margin-right:.5rem;display:flex;flex-direction:column;height:100%;grid-gap:.5rem;gap:.5rem}.decor-box :first-child{flex:4 1;background-color:aqua}.decor-box :nth-child(2){flex:3 1;background-color:#ff0}.decor-box :last-child{flex:2 1;background-color:blue}article{flex:10 0;overflow:scroll;text-align:center}article video{margin:.5rem auto}nav{display:flex;justify-content:space-between;grid-gap:.5rem;gap:.5rem}.collection-of-data h2{margin:.5rem auto}@media(min-width: 400px){.App{height:100vh}}@media(min-width: 768px){article{flex:20 0}article div section{width:80%;margin:auto}}@media(min-width: 1000px){article div section{width:60%;margin:auto}}
